@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.aem-Grid {
  display: grid;
  padding: 0 1rem;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.aem-Grid .aem-Grid {
  margin: auto -1rem;
}

.aem-Grid--default--12 {
  grid-template-columns: repeat(12, 1fr);
}

.aem-Grid-newComponent,
.aem-GridColumn {
  grid-column: calc(1 + 0) /
    calc(1 + var(--aem-grid-column-width));
  grid-column: calc(1 + var(--aem-grid-column-offset, 0)) /
    calc(1 + var(--aem-grid-column-width));
}

.aem-GridColumn--default--1 {
  --aem-grid-column-width: 1;
}
.aem-GridColumn--default--2 {
  --aem-grid-column-width: 2;
}
.aem-GridColumn--default--3 {
  --aem-grid-column-width: 3;
}
.aem-GridColumn--default--4 {
  --aem-grid-column-width: 4;
}
.aem-GridColumn--default--5 {
  --aem-grid-column-width: 5;
}
.aem-GridColumn--default--6 {
  --aem-grid-column-width: 6;
}
.aem-GridColumn--default--7 {
  --aem-grid-column-width: 7;
}
.aem-GridColumn--default--8 {
  --aem-grid-column-width: 8;
}
.aem-GridColumn--default--9 {
  --aem-grid-column-width: 9;
}
.aem-GridColumn--default--10 {
  --aem-grid-column-width: 10;
}
.aem-GridColumn--default--11 {
  --aem-grid-column-width: 11;
}
.aem-GridColumn--default--12 {
  --aem-grid-column-width: 12;
}

.aem-GridColumn--offset--default--0 {
  --aem-grid-column-offset: 0;
}
.aem-GridColumn--offset--default--1 {
  --aem-grid-column-offset: 1;
}
.aem-GridColumn--offset--default--2 {
  --aem-grid-column-offset: 2;
}
.aem-GridColumn--offset--default--3 {
  --aem-grid-column-offset: 3;
}
.aem-GridColumn--offset--default--4 {
  --aem-grid-column-offset: 4;
}
.aem-GridColumn--offset--default--5 {
  --aem-grid-column-offset: 5;
}
.aem-GridColumn--offset--default--6 {
  --aem-grid-column-offset: 6;
}
.aem-GridColumn--offset--default--7 {
  --aem-grid-column-offset: 7;
}
.aem-GridColumn--offset--default--8 {
  --aem-grid-column-offset: 8;
}
.aem-GridColumn--offset--default--9 {
  --aem-grid-column-offset: 9;
}
.aem-GridColumn--offset--default--10 {
  --aem-grid-column-offset: 10;
}
.aem-GridColumn--offset--default--11 {
  --aem-grid-column-offset: 11;
}

.aem-Grid-newComponent {
  --aem-grid-column-width: 12;
}

.cmp-contentfragment__title {
  margin-bottom: 1rem;
}

.cmp-contentfragment__elements { 
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
}
.search-by-category-container {
  overflow: hidden;
}

.search-by-category {
  margin: 1rem 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  display: flex;
  flex-direction: row;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.search-by-category-item {
  width: 30vw;
  min-width: 30vw;
  text-transform: uppercase;
  font-weight: bold;
}

.search-by-category-item-image {
  width: 25vw;
  height: 150px;
  object-fit: cover;
}

.search-by-category-item-image.selected {
  border: 3px solid #ffeb00;
}

.search-by-category-item a {
  color: white;
  text-decoration: none;
}

.homepage-hero-image-wrapper div:first-child,
.homepage-hero-image-wrapper .cmp-image,
.homepage-hero-image-wrapper .cmp-image__image {
  width: 100%;
  height: 100%;
}

.homepage-hero-image-wrapper .cmp-image__image {
  object-fit: cover;
}

.homepage-hero-image-wrapper .cmp-image__title {
  display: none;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Bebas Neue", sans-serif;
  font-weight: normal;
  font-size: 1.675rem;
  line-height: 1.1;
  margin: 0;
}

h1 { font-size: 1.675rem; }
h2 { font-size: 1.5rem; }
h3 { font-size: 1.375rem; }
h4, h5, h6 { 
  text-transform: uppercase; 
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; 
}
h4 { font-size: 1.25rem; }
h5 { font-size: 1.125rem; font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }
h6 { font-size: 1rem; font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.detail-screen h1,
.detail-screen h2,
.detail-screen h3,
.detail-screen h4,
.detail-screen h5,
.detail-screen h6 {
  margin: 0 0 1rem;
}

h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0 0 1rem;
}

html {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: normal;
}

#root {
  max-height: -webkit-fill-available;
  height: 100vh;
  overflow: hidden;
}

.loading-skeleton {
  animation: loading 1.5s infinite;
  background: linear-gradient(0.25turn, transparent, #373737, transparent),
    linear-gradient(#292929, #292929);
  background-position: 0 0;
}

@keyframes loading {
  to {
    background-position: 100vw 0;
  }
}

img {
  max-width: 100%;
}

.content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  height: 100%;
  width: 100vw;
}

.aem-AuthorLayer-Edit #root {
  min-height: 100vh;
  height: 100%;
  overflow: auto;
}

#app-container {
  display: grid;
  grid-template-rows: 1fr -webkit-max-content;
  grid-template-rows: 1fr max-content;
  min-height: 100vh;
  height: 100%;
  width: 100%;
}

html:not(.aem-AuthorLayer-Edit ) #app-container {
  min-height: -webkit-fill-available;
}

.aem-AuthorLayer-Edit #app-container {
  width: 100%;
}

